small {
  font-size: inherit; }
strong {
  font-weight: 700; }

h1, h2, h3, h4, h5, h6 {
  font-weight: 300; }

.title {
  // color: $c-title
  @include css-lock(21, 38, 320, 1399, font-size);
  font-weight: 300;
  line-height: 1;
  &--h3 {
    @include css-lock(18, 24, 320, 1399, font-size); }
  &--h4 {
    @include css-lock(16, 18, 320, 1399, font-size); }
  &--h5 {
    font-size: 1em !important;
    font-weight: 700; }
  &--white {
    color: $white; }
  &--ttl {
    color: $c-title; } }
