.nav {
  $this: &;
  li {
    display: inline-block;
    @include tr(color .3s);
    @include notlast {
      margin-right: 60px;
      @include xl {
        margin-right: 40px; }
      @include lg {
        margin-right: 0; } }
    @include lg {
      display: block;
      margin-bottom: .75em; } }
  a {
    @include tr(color .3s);
    @include hover {
      @extend #{$this}__item-current; } }
  &__item-current {
    color: $accent; }

  &--footer {
    li {
      display: block;
      @include notlast {
        margin-right: 0;
        margin-bottom: .7em;
        @include xl {
          margin-right: 0; }
        @include lg {
          margin-right: 0; } } }
    a {
      @include hover {
        @extend #{$this}__item-current; } }
    #{$this}__item-current {
      color: $white; } } }
